@use 'base/mixins' as m;
@use 'base/variables' as v;
.speakers{
    &__grid{
        @include m.contenedor;
        box-shadow: none;
        @include m.grid(1,5rem);
        @include m.tablet{
            @include m.grid(2,5rem);
            
        }

        @include m.desktop{
            @include m.grid(3,5rem);
            
        }
    }
}

.speaker{
    @include m.card;
    min-width: 350px;
    @include m.tablet{
        min-width: unset;
    }
    background-position: center top;
    background-size: 350px;
    background-repeat: no-repeat;

    @for $i from 0 to 4{
        &:nth-child(4n + #{$i}){
            background-image: -webkit-image-set(
                url('../img/bg_#{$i}.avif') 1x,
                url('../img/bg_#{$i}.webp') 1x,
                url('../img/bg_#{$i}.png') 1x
            );
        }
    }

    &__imagen{
        min-width: 350px;
        @include m.tablet{
            min-width: unset;
            
        }
    }
    &__ubicacion{
        color: v.$gris;
        margin: 1rem 0;
        font-size: 2rem;
        line-height: 1.5;
    }
    &__listado-skills{
        @include m.tags();
        >li{
            font-size: 1.3rem
        }
    }
}

.speakers-sociales{
    @include m.menu-social(v.$negro,2rem);
   display: flex;
   gap: 2rem;
   margin-top: 2rem;
}